const EmojiCtrl = require("emojiCtrl")
cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        input: cc.EditBox,
        emojiPanel: cc.Node,
        emojiItem: cc.Prefab,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        // cc.loader.loadRes("chat/emoji_action_texture", cc.SpriteAtlas, function (err, atlas) {
        //     cc.log("chat")
        //     cc.vv.emjioAtlas = atlas
            let emojiList = cc.vv.configs.emojiList
            for (let emoji of emojiList) {
                let emojiItem = cc.instantiate(this.emojiItem)
                emojiItem.parent = this.emojiPanel
                emojiItem.getComponent(EmojiCtrl).setEmojiInfo(emoji)
            }
        // }.bind(this))
    },

    start() {

    },

    sendMessage() {
        if (this.input.string.length > 0) {
            cc.log("chat")
            cc.log(this.input.string)
            cc.vv.net.send("chat", this.input.string);
            this.input.string = ""
        }
    },

    closeChatPanel() {
        this.node.active = false
    }
    // update (dt) {},
});
